.movies .movie {
    width: 45%;
    background-color: rgb(41, 40, 40);
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 10px 15px 15px #1d1c1c;
}

.movie img {
    max-width: 180px;
    margin-right: 30px;
    position: relative;
    top: -50px;
    width: 100%;
    box-shadow: 5px 10px 10px #000000;
}

.movie .movie_title,
.movie .movie_year {
    margin: 0;
    font-weight: 300;
}

.movie .movie_title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #ffffff;
}

.movie .movie_genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    font-size: 14px;
    margin: 5px 0px;
}

.movie .movie_genres li {
    margin-right: 10px;
}